import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { OmgdcElement } from "./OmgdcElement";

export class Bounds extends OmgdcElement {
    constructor(model: BpmnModel) {
        super(model);
    }

    Height: number;
    Width: number;
    X: number;
    Y: number;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.Height = 0;
        this.Width = 0;
        this.X = 0;
        this.Y = 0;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "Height", this.Height);
        BpmnModelHelper.SetAttributeValue(obj, "Width", this.Width);
        BpmnModelHelper.SetAttributeValue(obj, "X", this.X);
        BpmnModelHelper.SetAttributeValue(obj, "Y", this.Y);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.Height = obj.Height;
        this.Width = obj.Width;
        this.X = obj.X;
        this.Y = obj.Y;
    }
}